
import DownloadLinks from '../Elements/DownloadLinks.'
import ForgetPhoto from "../../../assets/Images/Screenshot_20250124_165516_com.qualityconnect 1.png";
import "../../Style/SectionTwo.css"
import "../../Style/FQSection.css"
const FQSection = ({className}:any) => {
  return (
    <section className={`section-two-container ${className} `}>
        <div className="content-wrapper">
          <h1 className="section-title">
            لا تشيل هم الكاش أو البطاقات كل معاملاتك في جوالك
          </h1>
          <p className="section-description">
            قد يكون التعامل مع الكاش والبطاقات أمرًا مرهقًا وغير عملي مع محفظة
            شلن، يمكنك تجميع كل احتياجاتك المالية في مكان واحد! لا حاجة لحمل
            المحفظة التقليدية بعد الآن، فشلن توفر لك حلاً رقمياً آمناً وسهلاً
            لإجراء كافة معاملاتك اليومية. من تحويل الأموال إلى الدفع للمشتريات،
            شلن تجعل حياتك أكثر سهولة وراحة."
          </p>
          <DownloadLinks />
        </div>
        <div className="image-wrapper">
          <img src={ForgetPhoto} alt="" />
        </div>
      </section>
  )
}

export default FQSection
